body {
  font-family: sans-serif;
}

.products-container {
  justify-content: space-evenly;
  align-items: center;
}

.products-container ul {
  list-style-type: none;
  padding: 20px;
}

.products-container ul li {
  display: inline-flex;
  padding: 10px;
  margin-right: 20px;
  border: 2px solid #333;
}

.product-type {
  font-size: 20px;
  display: block;
}
